body {
  margin: 0;
  padding: 0;
  background-color: black;
  color: white;
}

#texts {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-left: 20px;
}

#texts input {
  margin: 0;
  background-color: black;
  color: white;
}

#texts p {
  margin-top: 0;
  font-size: 18px;
  text-align: 'center';
}

#range {
  /* box-shadow: 0 0 5px 5px white; */
  cursor: pointer;
  width: 190px;
}

#range:hover, #rangeSpeed:hover {
  box-shadow: 0 0 5px 5px #3071a9;
}

#rangeSpeed {
  cursor: pointer;
  width: 230px;
  /* transform: rotate(180deg); */
}